<template>
    <section class="section has-background-primary" id="contact">
        <div class="container has-text-centered">
            <h1 class="title has-text-white">Contact</h1>
            <div v-if="success" class="notification is-success column is-8 is-offset-2">
                Message sent succesfully
            </div>
            <div v-if="error" class="notification is-danger column is-8 is-offset-2">
                Error when sending the message
            </div>
            <div class="columns">
                <form id="contact-form" class="column is-8 is-offset-2" @submit.prevent="sendEmail">
                    <div class="field">
                        <div class="control">
                            <input required class="input" type="text" name="user_name" placeholder="Name">
                        </div>
                    </div>
                    <div class="field">
                        <div class="control">
                            <input required class="input" type="email" name="user_email" placeholder="Email">
                        </div>
                    </div>
                    <div class="field">
                        <div class="control">
                            <textarea required class="textarea" name="text" placeholder="Message"></textarea>
                        </div>
                    </div>
                    <div class="field">
                        <div class="control has-text-centered">
                            <button v-if="sending" class="button is-warning is-medium is-loading">
                                Loading
                            </button>
                            <button v-else class="button is-warning is-medium">
                                Send
                            </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </section>
</template>

<script>
import { sendForm } from 'emailjs-com'

export default {
  data () {
    return {
      sending: false,
      success: false,
      error: false
    }
  },
  methods: {
    sendEmail (e) {
      this.success = false
      this.error = false
      const form = e.target
      if (this.validate(form)) {
        this.sending = true
        sendForm('contact_service', 'contact_form', form, 'user_NGWfk3MGqEChi3Bg6GX9X')
          .then((response) => {
            this.sending = false
            form.reset()
            this.success = true
          })
          .catch(() => {
            this.sending = false
            this.error = true
          })
      }
    },
    validate (form) {
      const name = form.user_name.value.trim()
      const email = form.user_email.value.trim()
      const message = form.text.value.trim()

      return (name !== '' && email !== '' && message !== '')
    }
  }
}
</script>
